import React, { useContext, useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Link, NavLink } from "react-router-dom";
import { StoreContext } from "../store/StoreProvider";
import Search from "../components/Search/Search";
import $ from "jquery";

const MainNav = () => {
  const [bodyFixed, setBodyFixed] = useState(false);
  const { cartStatus, setCategory, isLoggedIn, setGuest, setInCheckout } =
    useContext(StoreContext);

  $(".closeNav").click(function () {
    $(".navbar-collapse").collapse("hide");
  });

  // $(".navbar-collapse a").click(function () {
  //   $(".navbar-collapse").collapse("hide");
  // });

  const shopLi = (
    <li className="nav-item dropdown">
      <NavLink
        className="nav-link dropdown-toggle"
        to="#"
        id="navbarDropdown"
        role="button"
        data-toggle="dropdown"
        aria-haspopup="true"
        aria-expanded="false"
      >
        Sklep
      </NavLink>
      <div
        className="dropdown-menu"
        aria-labelledby="navbarDropdown d-flex flex-column"
      >
        <Link
          className="dropdown-item closeNav"
          to="/shop"
          onClick={() => {
            setCategory("all");
          }}
        >
          Wszystkie produkty
        </Link>
        <div className="dropdown-divider"></div>
        <Link
          className="dropdown-item closeNav"
          to="/shop/mens-clothing/all"
          onClick={() => {
            setCategory(`men's clothing`);
          }}
        >
          Moda męska
        </Link>
        <Link
          className="dropdown-item closeNav"
          to="/shop/jewelery/all"
          onClick={() => {
            setCategory("jewelery");
          }}
        >
          Biżuteria
        </Link>
        <Link
          className="dropdown-item closeNav"
          to="/shop/electronics/all"
          onClick={() => {
            setCategory("electronics");
          }}
        >
          Elektronika
        </Link>
        <Link
          className="dropdown-item closeNav"
          to="/shop/womens-clothing/all"
          onClick={() => {
            setCategory(`women's clothing`);
          }}
        >
          Moda damska
        </Link>
      </div>
    </li>
  );

  return (
    <nav className="navbar navbar-expand-lg sticky-top navbar-light bg-light">
      <div className="container fullsize">
        <Link className="navbar-brand closeNav" to="/">
          MainShop
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
          onClick={() => {
            // setBodyFixed(!bodyFixed);
            // bodyFixed
            //   ? (document.body.style.height = "")
            //   : (document.body.style.height = "100%");
            // bodyFixed
            //   ? (document.body.style.overflow = "")
            //   : (document.body.style.overflow = "hidden");
            // bodyFixed
            //   ? (document.querySelector(".fullsize").style.height = "")
            //   : (document.querySelector(".fullsize").style.height =
            //       "calc(100vh - 16px)");
          }}
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav mr-auto">
            <li className="nav-item">
              <NavLink className="nav-link closeNav" to="/about">
                O nas
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link closeNav" to="/contact">
                Kontakt
              </NavLink>
            </li>
            {shopLi}
          </ul>
          <Search />
          <ul className="navbar-nav">
            <li className="nav-item">
              <NavLink
                className="nav-link closeNav"
                onClick={() => {
                  setGuest(false);
                  setInCheckout(false);
                }}
                to={isLoggedIn ? "/account" : "/welcome"}
              >
                {isLoggedIn ? "Twoje konto" : "Zaloguj"}
              </NavLink>
            </li>
            <li className="nav-item closeNav">
              <NavLink className="nav-link" to="/favourites">
                Ulubione
              </NavLink>
            </li>
            <li className="nav-item closeNav">
              <NavLink className="nav-link" to="/shopping-cart">
                Koszyk ({cartStatus.length})
              </NavLink>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default MainNav;
