import React, { useContext, useEffect, useState } from "react";
import { StoreContext } from "../../../store/StoreProvider";

const UserInfo = () => {
  const [editMode, setEditMode] = useState(false);
  const [userNameAndSurname, setUserNameAndSurname] = useState();
  const [userAdress, setUserAdress] = useState();
  const [userPostcodeCity, setUserPostcodeCity] = useState();
  const [userPhone, setUserPhone] = useState();
  const { activeUser, guestData } = useContext(StoreContext);

  const userData = activeUser;
  const guestInfoData = guestData;

  const handleNameAndSurnameChange = (e) => {
    const data = e.target.value;
    setUserNameAndSurname(data);
  };
  const handleAdressChange = (e) => {
    const data = e.target.value;
    setUserAdress(data);
  };
  const handlePostcodeCityChange = (e) => {
    const data = e.target.value;
    setUserPostcodeCity(data);
  };
  const handlePhoneChange = (e) => {
    const data = e.target.value;
    setUserPhone(data);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (userNameAndSurname && userAdress && userPostcodeCity && userPhone) {
      setEditMode(!editMode);
      if (userData) {
        let editedUser = activeUser;
        editedUser.nameAndSurname = userNameAndSurname;
        editedUser.adress = userAdress;
        editedUser.postCodeCity = userPostcodeCity;
        editedUser.phone = userPhone;
      }
      if (guestInfoData) {
        let editedUser = guestInfoData;
        editedUser.nameAndSurname = userNameAndSurname;
        editedUser.adress = userAdress;
        editedUser.postCodeCity = userPostcodeCity;
        editedUser.phone = userPhone;
      }
    } else {
      alert("Uzupełnij wszystkie wymagane dane");
    }
  };
  useEffect(() => {
    if (userData) {
      setUserNameAndSurname(userData.nameAndSurname);
      setUserAdress(userData.adress);
      setUserPostcodeCity(userData.postCodeCity);
      setUserPhone(userData.phone);
    }
    if (guestInfoData) {
      setUserNameAndSurname(guestInfoData.nameAndSurname);
      setUserAdress(guestInfoData.adress);
      setUserPostcodeCity(guestInfoData.postCodeCity);
      setUserPhone(guestInfoData.phone);
    }
  }, []);

  return (
    <form className="d-flex flex-column" onSubmit={handleSubmit}>
      <small className="text-muted py-1">Imię i nazwisko</small>
      <input
        onChange={handleNameAndSurnameChange}
        type="text"
        disabled={!editMode}
        value={userNameAndSurname}
        className="form-control"
      />
      <small className="text-muted py-1">Adres</small>
      <input
        onChange={handleAdressChange}
        type="text"
        disabled={!editMode}
        value={userAdress}
        className="form-control"
      />
      <small className="text-muted py-1">Miejscowość</small>
      <input
        onChange={handlePostcodeCityChange}
        type="text"
        disabled={!editMode}
        value={userPostcodeCity}
        className="form-control"
      />
      <small className="text-muted py-1">Numer telefonu</small>
      <input
        onChange={handlePhoneChange}
        type="text"
        disabled={!editMode}
        value={userPhone}
        className="form-control"
      />
      {editMode === false && (
        <button
          type="button"
          onClick={() => setEditMode(!editMode)}
          className="btn btn-primary mt-4"
        >
          Edytuj dane
        </button>
      )}
      {editMode === true && (
        <button type="submit" className="btn btn-primary mt-4">
          Zakończ edycję
        </button>
      )}
    </form>
  );
};

export default UserInfo;
